import React from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import SectionHeader from "components/SectionHeader";
import moment from "moment";
import { toCurrency } from "Utils/numberFormat";
import { DEFAULT_DATE_TIME_FORMAT } from "Utils/setting";
import { makeStyles } from "@material-ui/core/styles";
import { Transaction } from "models/Transaction";

import styles from "./styles.module.scss"

interface TransactionHistorySectionProps {
  transactions: Array<Transaction>
}

const useStyles = makeStyles({
  createAt: {
    width: "25%"
  }
});

const TransactionHistorySection: React.FunctionComponent<TransactionHistorySectionProps> = ({ transactions }) => {
  const classes = useStyles();
  return (
    <>
      <SectionHeader title="Transaction History" />
      <Table>
        <TableBody>
          {transactions.map(
            ({
              _id,
              createdAt,
              status,
              amount,
              paymentMethod: { name: paymentName }
            }) => (
                <TableRow key={_id}>
                  <TableCell className={classes.createAt}>
                    {moment(createdAt).format(DEFAULT_DATE_TIME_FORMAT)}
                  </TableCell>
                  <TableCell>{paymentName}</TableCell>
                  <TableCell>{toCurrency(amount)}</TableCell>
                  <TableCell className={styles.status}>{status}</TableCell>
                </TableRow>
              )
          )}
        </TableBody>
      </Table>
    </>
  );
};

export default TransactionHistorySection;