import React, { useEffect } from "react";
import "date-fns";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import DashboardApiClient from "api/agency/DashboardApiClient";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import { toCurrency } from "Utils/numberFormat";
import DateFnsUtils from "@date-io/date-fns";
import { KeyboardDatePicker, MuiPickersUtilsProvider, } from "@material-ui/pickers";
import { Transaction } from "models/Transaction";
import { DEFAULT_DATE_TIME_FORMAT } from "Utils/setting";
import LanguageSwitcher from "components/LanguageSwitcher";
import AgencyHeader from "components/AgencyHeader";
import moment from "moment";

import * as styles from "./styles.module.scss";

const dashboardApiClient = new DashboardApiClient();

interface AgencyPageContainerProps {
  transactions: Array<Transaction>;
}

const AgencyPageContainer: React.FC<AgencyPageContainerProps> = ({ transactions }) => {
  const [selectedDate, setSelectedDate] = React.useState<Date>(new Date("2014-08-18T21:11:54"));
  const [deposit, setDeposit] = React.useState<string>("");

  const handleDateChange = (date: Date) => {
    setSelectedDate(date);
  };

  useEffect(() => {
    dashboardApiClient
      .getInformationDashboard()
      .then(res => setDeposit(res.deposit));
  }, []);

  return (
    <>
      <AgencyHeader />
      <Box p={4}>
        <Grid container spacing={3}>
          <Grid item xs={4}>
            <Card className={styles.card}>
              <CardContent className={styles.cardContent}>
                <Box>
                  <Typography variant="h5">
                    Total Deposit
                  </Typography>
                  <Typography>
                    {toCurrency(deposit)}
                  </Typography>
                </Box>
                <Box>
                  <Typography>
                    Last charged
                  </Typography>
                  <Typography>
                    2020.05.31
                  </Typography>
                  <Typography>
                    12:34:33
                  </Typography>
                </Box>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={8}>
            <Box display="flex" flexDirection="row-reverse">
              <LanguageSwitcher />
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Box className={styles.tableAction}>
              <Box className={styles.btnGroup}>
                <ButtonGroup variant="text" color="primary" aria-label="text primary button group">
                  <Button>Deposit</Button>
                  <Button>Withdraw</Button>
                </ButtonGroup>
              </Box>
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <Grid container className={styles.dateContainer}>
                  <KeyboardDatePicker
                    className={styles.startDate}
                    margin="normal"
                    id="date-picker-dialog"
                    label="From"
                    format="MM/dd/yyyy"
                    value={selectedDate}
                    onChange={(event: any) => handleDateChange(event)}
                    KeyboardButtonProps={{
                      "aria-label": "change date",
                    }}
                  />
                  <KeyboardDatePicker
                    margin="normal"
                    id="date-picker-dialog"
                    label="To"
                    format="MM/dd/yyyy"
                    value={selectedDate}
                    onChange={(event: any) => handleDateChange(event)}
                    KeyboardButtonProps={{
                      "aria-label": "change date",
                    }}
                  />
                </Grid>
              </MuiPickersUtilsProvider>
            </Box>
            <TableContainer component={Paper}>
              <Table aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell align="left">Date</TableCell>
                    <TableCell align="left">User ID</TableCell>
                    <TableCell align="left">Type</TableCell>
                    <TableCell align="left">From User</TableCell>
                    <TableCell align="left">Commission</TableCell>
                    <TableCell align="left">Payable</TableCell>
                    <TableCell align="left">Status</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {transactions.map((transaction) => (
                    <TableRow key={transaction._id}>
                      <TableCell align="left">{moment(transaction.updatedAt).format(DEFAULT_DATE_TIME_FORMAT)}</TableCell>
                      <TableCell align="left">{transaction.merchandiser ? transaction.merchandiser._id : "N/A"}</TableCell>
                      <TableCell align="left">{transaction.paymentMethod.name}</TableCell>
                      <TableCell align="left">{transaction.senderAccountInfo}</TableCell>
                      <TableCell align="left">{toCurrency(transaction.amount * 0.035)}</TableCell>
                      <TableCell align="left">{toCurrency(transaction.amount * 0.965)}</TableCell>
                      <TableCell align="left">{transaction.status}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

export default AgencyPageContainer;
