import React from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Button from "@material-ui/core/Button";
import SectionHeader from "components/SectionHeader";
import withStyles from "@material-ui/core/styles/withStyles";
import * as styles from "./styles.module.scss";
import wechatIcon from "./wechat.png";
import alipayIcon from "./alipay.png";

interface MethodSectionProps {
  method: string, 
  amount:number | string,
  onChange: Function,
  onPay: Function
}

const StyledRadioGroup = withStyles(() => ({
  root: {
    flexDirection: "row",
    alignItems: "center",
    margin: "30px 0"
  }
}))(RadioGroup);

const StyledFormControlLabel = withStyles(() => ({
  root: {}
}))(FormControlLabel);

const MethodSection: React.FunctionComponent<MethodSectionProps> = ({ method, amount, onChange, onPay }) => {
  return (
    <>
      <SectionHeader title="Method" />
      <StyledRadioGroup
        aria-label="gender"
        name="gender1"
        value={method}
        onChange={event => onChange(event)}
      >
        <StyledFormControlLabel
          value="wechat"
          control={<Radio />}
          label={
            <img
              className={styles.imgLabel}
              src={wechatIcon}
              alt="wechat-pay"
            />
                    }
        />
        <StyledFormControlLabel
          value="alipay"
          control={<Radio />}
          label={
            <img className={styles.imgLabel} src={alipayIcon} alt="alipay" />
                    }
        />
        <StyledFormControlLabel
          value="bank"
          control={<Radio />}
          label="BANK TRANSFER"
        />
        <Button
          variant="contained"
          color="primary"
          disabled={!amount || !method}
          onClick={event => onPay(event)}
        >
          Pay
        </Button>
      </StyledRadioGroup>
    </>
  );
};

export default MethodSection;
