import React, { useEffect } from "react";
import AgencyPageContainer from "containers/AgencyPageContainer";
import TransactionApiClient from "api/buyer/BuyerTransactionApiClient";
import withAuth from "components/withAuth";
import { Transaction } from "../../models/Transaction";

const transactionApiClient = new TransactionApiClient();

const AgencyPage: React.FunctionComponent = () => {
  const [transactions, setTransactions] = React.useState<Array<Transaction>>([]);

  useEffect(() => {
    transactionApiClient
      .getAllTransactions("submitted")
      .then(trans => setTransactions(trans.docs));
  }, []);
  return <AgencyPageContainer transactions={transactions} />;
};

export default withAuth(AgencyPage, "agency");